@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Roboto&display=swap');
*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: 'Libre Baskerville', serif;
}
.content{
  height: 70vh;
  background-color: rgb(223, 219, 213);
}
.navbar{
  background-color: rgb(210, 122, 122) !important;
}
.register-page{
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh ;
    background-color: rgb(231, 211, 232);
}
.heading{
  text-align: center;
}
.FormPage{
  background-color: rgb(115, 111, 151);
  padding: 20px;
  border-radius: 8px;
}
.RegisImg{
  width: 440px;
  height: 250px;
  z-index: 15;
  border-radius: 6px;
}
.labelBox{
  font-weight: bold;
}
.box{
  padding: 4px;
  font-size: 16px;
  background-color: rgb(229, 218, 218);
}
/* transection page  */
.filters{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 15px 20px;
  box-shadow: 0 0 3px rgb(130, 72, 72);
}

 /* login form */
.loginForm{
    background-color: bisque;
    border-radius: 8px;
}
.formPage{
  padding: 40px;
}

/* antd icon */
.switch-icons{
  border: 3px solid #d1cece;
  border-radius: 6px;
  padding: 5px 6px;
  background-color: rgb(210, 211, 218);
}
.anticon svg{
   font-size: 20px;
   cursor: pointer;
}
.active-icon{
  color: black;
}
.inactive-icon{
  color: gray;
}
.fixed-bottom {
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
}
.footerIcon{
  display: flex;
  justify-content:center;
  cursor: pointer;
}
.icon{
  margin: 12px;
}

.card:hover {
  transform: scale(1.05);
  transition: transform 0.3s ease-in-out;
}

